<template>
  <div>
    <template>
      <el-button type="primary">
        <el-upload
        class="upload-demo"
        action=""
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        multiple
        >上传文件</el-upload>
      </el-button>
      <el-button>新建文件夹</el-button>
      <el-button>添加云盘</el-button>
      <el-input placeholder="请输入内容"
                v-model="input"
                clearable
                style="width:200px"></el-input>
      <el-button>搜索</el-button>
    </template>
    <template>
      <el-table  highlight-current-row  style="width: 100%;">
        <el-table-column prop="name" label="文件名" width="150">
        </el-table-column>
        <el-table-column prop="size" label="大小" width="100">
        </el-table-column>
        <el-table-column prop="operate" label="类型" width="100">
        </el-table-column>
        <el-table-column prop="date" label="创建时间" width="100">
        </el-table-column>
        <el-table-column prop="detail" label="源" width="100">
        </el-table-column>
        <el-table-column prop="detail" label="操作" width="100">
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        input: '',
        fileList:[],
        list: [],
        current: {
          dir: '/',
          page: '1',
          num: '100',
          desc: '1',
          order: 'size'
        },
        bread: [
          {
            path: '/',
            desc: '全部文件'
          }
        ],
        dir_name: '',
        search: {
          key: '',
          recursion: '1',
          order: 'time',
          page: '1',
          num: '100'
        },
        cloud_err: false,
        cloud_err_msg: '',
        object_urls: [],
        object_name: '',
        download_info: {
          urls: [],
          name: ''
        },
        fileMD5: '',
        show_modal: {
          download: false,
          folder: false,
          osstoken: false,
          md5: false
        },
        oss_value: [
          'baidu'
        ],
        oss_options: [
          'baidu',
          'aliyun',
          'ksyun'
        ]
      }
    },
    created() {
      // if (typeof this.$route.query['dir'] === 'undefined') {
      //   this.$router.push({path: 'cloud', query: {vmode: 'list', dir: this.current.dir}});
      // } else {
      //   this.current.dir = this.$route.query['dir'];
      // }
      // this.fetch_data();
      this.register();
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      register() {
        this.$http.post('/api/register',
          {   username: "1",
              password: "2"
          },
          //options
          {
            headers: {'Content-Type':'application/json; charset=UTF-8'}
          }                 ).then((response) => {
          // 响应成功回调
          var data = response.body;
          if (data.status=='success') {
            alert('Success! ' + data.msg)
          }
          else {
            alert(data.msg)
          }
          this.password = ''
        }, (response) => {
          // 响应错误回调
          alert('注册出错了！ '+ JSON.stringify(response))
        });
      },
      fetch_data: function () {
        var params = this.current;
        this.$http.get('/api/cloud/list/', {params}).then((response) => {
          response = response.data;
          if (response.errno === ERR_OK) {
            this.list = response.data['nodedir'];
            // Set path
            var prefix = this.current.dir.length === 1 ? '' : this.current.dir;
            this.list.forEach(function(item, index) {
              item.path = prefix + '/' + item.name;
            });
            // Set bread;
            var start = 1;
            var tmp = [{path: '/', desc: '全部文件'}];
            while (true) {
              var ind = this.current.dir.indexOf('/', start);
              if (ind !== -1) {
                tmp.push({path: this.current.dir.substr(0, ind), desc: this.current.dir.substr(start, ind - start)});
                start = ind + 1;
              } else {
                if (this.current.dir !== '/') {
                  tmp.push({path: this.current.dir, desc: this.current.dir.substr(start)});
                }
                break;
              }
            }
            this.bread = tmp;
          } else {
            this.$toast('获取失败 ' + response.err_msg.error, {horizontalPosition: 'center', duration: 1000});
          }
        });
      },
    }
  }
</script>

<style>
</style>



